img {
  pointer-events: none;
}
.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  padding: 10px;
  position: relative;
  margin-bottom: 7vh;
}

.grid-item {
  position: relative;
  height: 100%;
}

.item {
  width: 100%;
  text-align: center;
  transition: box-shadow 0.3s;
}

.item.top {
  position: absolute;
  top: 5%;
}

.item.bottom {
  position: absolute;
  bottom: 5%;
}

.expandedItem {
  text-align: center;
}

.blurred .item {
  filter: blur(5px);
  pointer-events: none;
}

.footer {
  width: 100vw;
  background-color: #333;
  color: #fff;
  padding: 20px;
  margin: 10px;
}

.footer-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.company-info, .social-links {
  flex: 1;
  min-width: 200px;
}

.company-info h3, .social-links h3 {
  font-size: 18px;
  margin: 10px;
}

.social-icons {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social-icons li {
  display: inline-block;
  margin: 10px;
}

.social-icons li:last-child {
  margin: 0;
}

.social-icons a {
  color: #fff;
  font-size: 20px;
}

.footer-bottom {
  text-align: center;
  padding-top: 10px;
  border-top: 1px solid #555;
  margin: 10px;
}

.banner {
  width: 100%;
  height: auto;
}

@media (max-width: 1500px) {
  .container {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    padding: 30px;
  }

  .item {
    padding: 30px;
  }

  .expandedItem {
    padding: 60px;
  }
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    padding: 10px;
  }

  .item {
    padding: 10px;
  }

  .expandedItem {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .container {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 5px;
  }

  .item {
    padding: 5px;
  }

  .expandedItem {
    padding: 10px;
  }
}
