.cart-container {
    width: 60%;
    margin: 50px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .cart-container h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .cart-items {
    margin-bottom: 20px;
  }
  
  .cart-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 10px;
    border-bottom: 1px solid #ccc;
  }
  
  .item-details {
    flex: 1;
  }
  
  .item-price {
    flex: 0 0 100px;
    text-align: right;
  }
  
  .cart-total {
    text-align: right;
    margin-bottom: 20px;
  }
  
  .checkout-button {
    display: block;
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .checkout-button:hover {
    background-color: #0056b3;
  }
  