.about-us-container {
    width: 80%;
    margin: 50px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .about-us-container h1 {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .introduction, .mission-values, .team {
    margin-bottom: 30px;
  }
  
  h2 {
    color: #333;
    margin-bottom: 15px;
  }
  
  p {
    line-height: 1.6;
    color: #555;
  }
  
  ul {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  ul li {
    margin-bottom: 10px;
  }
  
  .team-member {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .team-image {
    border-radius: 50%;
    margin-right: 20px;
  }
  
  .team h3 {
    margin: 0;
  }
  
  .team p {
    margin: 0;
    color: #777;
  }
  